<template>
  <div>
    <LayoutBox one="邮费管理" two="邮费规则列表">
      <el-form size="small" inline>
        <el-form-item>
          <el-button type="danger" icon="el-icon-plus" @click="dialogPostFeeAdd = true">创建规则</el-button>
        </el-form-item>
      </el-form>

      <el-table :data="tableData" border size="small">
        <el-table-column label="起始价" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.nationalRule[0] }}</span>
          </template>
        </el-table-column>
        <el-table-column label="结束价" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.nationalRule[1] }}</span>
          </template>
        </el-table-column>
        <el-table-column label="邮费" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.nationalRule[2] }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="150">
          <template slot-scope="scope">
            <el-button type="success" size="small" @click="onEdit(scope)">编辑</el-button>
            <el-button type="info" size="small" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </LayoutBox>

    <el-dialog title="邮费规则" :visible.sync="dialogPostFeeAdd" width="500px" :before-close="handleClose">
      <el-form ref="postFeeForm" size="small" :model="postFeeForm" label-width="100px">
        <el-form-item label="价格范围" required>
          <el-input v-model="postFeeForm.startPrice" style="width: 100px" /> ~ <el-input v-model="postFeeForm.endPrice" style="width: 100px" />
          <span> 运费：</span>
          <el-input v-model="postFeeForm.postFee" style="width: 80px"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogPostFeeAdd = false">取消</el-button>
        <el-button v-if="postFeeForm.id" type="primary" size="small" @click="onSave">保存</el-button>
        <el-button v-else type="primary" size="small" @click="onSubmit">提交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_POSTEETPOSTRULE_API, MIXINS_POSTADD_API, MIXINS_GETPOSTRULE_API, MIXINS_POSTDELETE_API, MIXINS_POSTEDIT_API } from '@/mixins/api'

export default {
  name: 'postFeeList',

  components: {
    LayoutBox
  },

  mixins: [
    MIXINS_POSTEETPOSTRULE_API,
    MIXINS_POSTADD_API,
    MIXINS_GETPOSTRULE_API,
    MIXINS_POSTDELETE_API,
    MIXINS_POSTEDIT_API
  ],

  data() {
    return {
      tableData: [],
      queryInit: { // 初始化分页
        no: 1,
        size: 10,
        total: 0,
      },
      dialogPostFeeAdd: false, // 打开添加邮费规则窗口
      postFeeForm: {
        id: '',
        startPrice: '',
        endPrice: '',
        postFee: ''
      }, // 邮费规则表单
    }
  },

  methods: {
    // 分页每页显示多少条
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    // 分页选择显示的是第几页
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },
    // 点击编辑
    onEdit (scope) {
      this.apiGetPostRule(scope.row.id)
    },
    // 点击删除
    onDelete (scope) {
      this.$confirm('此操作将永久删除该规则, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let data = {
          ruleIds: scope.row.id
        }
        this.postDelete(data).then((res) => {
          if (res.data.success) {
            this.tableData.splice(scope.$index, 1)
            this.$message.success('操作成功')
          } else {
            this.$message.error(res.data.msg)
          }
        })
      })
    },
    // 点击提交
    onSubmit () {
      if (this.postFeeForm.startPrice === '' || this.postFeeForm.endPrice === '' || this.postFeeForm.postFee === '') {
        return this.$message.error('请把价格范围填写完整')
      }
      let data = {
        nationalRules: `${this.postFeeForm.startPrice},${this.postFeeForm.endPrice},${this.postFeeForm.postFee}`
      }
      this.postAdd(data).then((res) => {
        if (res.data.success) {
          this.$message.success('提交成功')
          this.dialogPostFeeAdd = false
          this._initPostFeeForm()
          this.apiPostEetPostRule()
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    // 点击保存
    onSave () {
      if (this.postFeeForm.startPrice === '' || this.postFeeForm.endPrice === '' || this.postFeeForm.postFee === '') {
        return this.$message.error('请把价格范围填写完整')
      }
      let data = {
        id: this.postFeeForm.id,
        nationalRules: `${this.postFeeForm.startPrice},${this.postFeeForm.endPrice},${this.postFeeForm.postFee}`
      }
      this.postEdit(data).then((res) => {
        if (res.data.success) {
          this.$message.success('保存成功')
          this.dialogPostFeeAdd = false
          this._initPostFeeForm()
          this.apiPostEetPostRule()
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    // 关闭邮费规则窗口
    handleClose () {
      this._initPostFeeForm()
      this.dialogPostFeeAdd = false
    },

    // 私有方法————-----------------------------------------
    _initPostFeeForm () {
      this.postFeeForm = {
        id: '',
        startPrice: '',
        endPrice: '',
        postFee: ''
      }
    },

    // api-----------------------------------
    // 分页查询邮费规则
    apiPostEetPostRule () {
      let data = {
        ...this.queryInit
      }
      this.postEetPostRule(data).then((res) => {
        console.log(res)
        if (res.data.success) {
          this.queryInit.total = res.data.data[0].total
          this.tableData = res.data.data.map((item) => {
            return {
              nationalRule: item.nationalRule.split(','),
              id: item.id
            }
          })
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    // 根据id单查邮费规则
    apiGetPostRule (id) {
      this.getPostRule(id).then((res) => {
        if (res.data.success) {
          let postFeeForm = res.data.data.nationalRule.split(',')
          this.postFeeForm = {
            id: res.data.data.id,
            startPrice: postFeeForm[0],
            endPrice: postFeeForm[1],
            postFee: postFeeForm[2]
          }
          this.dialogPostFeeAdd = true
        } else {
          this.$message.error(res.data.msg)
        }
      })
    }
  },

  created () {
    this.apiPostEetPostRule()
  },
}
</script>

<style lang="scss" scoped>
  .el-collapse{
    border: 0px;
  }
</style>
